.footer {
  background-color: $footer-color;
  padding-top: 54px;
  padding-bottom: 52px;

  @media (min-width: $vp-768) {
    padding-top: 0;
    padding-bottom: 0;
  }
}

.footer__wrapper {
  @media (min-width: $vp-768) {
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin: 46px auto 28px;
  }
}

.footer__container {
  display: block;
  column-count: 2;
  column-gap: 30px;

  @media (min-width: $vp-768) {
    display: flex;
    justify-content: space-between;
    align-items: end;
    gap: 62px;
  }

  @media (min-width: $vp-1200) {
    align-items: start;
    gap: 277px;
  }
}

.footer__logo {
  display: inline-block;
  padding-bottom: 11px;

  &:hover {
    opacity: 0.3;
  }

  @media (min-width: $vp-768) {
    align-self: start;
    padding: 0;
    padding-top: 5px;
  }
}

.footer__logo-img {
  @media (min-width: $vp-768) {
    min-width: 82px;
    height: 20px;
  }

  @media (min-width: $vp-1200) {
    margin-top: -8px;
    min-width: 88px;
    height: 44px;
  }
}

.footer__nav {
  display: flex;
  gap: 73px;

  @media (min-width: $vp-768) {
    gap: 30px;
  }

  @media (min-width: $vp-1200) {
    gap: 40px;
  }
}

.footer__list {
  margin: 0;
  padding: 0;
  display: block;
  list-style-type: none;

  @media (min-width: $vp-768) {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 10px 30px;
  }

  @media (min-width: $vp-1200) {
    gap: 10px 40px;
    margin-top: -5px;

    &:last-of-type {
      margin-top: -5px;
    }
  }
}

.footer__item {
  margin-bottom: 16px;

  & a {
    display: inline-block;
    color: $color-default-white;
    font-size: 16px;
    font-weight: 400;
    line-height: normal;
    position: relative;
    transition: $trans-default;

    @media (min-width: $vp-768) {
      font-size: 14px;
    }

    &:hover::after {
      position: absolute;
      content: "";
      bottom: -5px;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: $color-default-white;
    }

    &:focus-visible {
      outline-offset: 10px;
      outline: 1px solid $color-default-white;
    }

    &:disabled {
      opacity: 0.4;
    }
  }
}

.footer__social {
  margin: 0;
  padding: 0;
  padding-top: 15px;
  list-style-type: none;
  display: flex;
  gap: 11px;

  @media (min-width: $vp-768) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    padding-top: 0;
    align-self: center;
    justify-content: space-between;
    margin-top: -11px;
  }

  @media (min-width: $vp-1200) {
    margin-top: -22px;
  }
}

.footer__social-item {
  a {
    display: block;
    height: 25px;
  }

  svg {
    fill: inherit;
    border-radius: 50%;
    background-color: $color-default-white;
    transition: $trans-default;
  }

  &:hover svg,
  &:focus-visible svg {
    fill: $color-default-white;
    background-color: $price;
  }

  &:active svg {
    fill: $color-default-white;
    background-color: $gym-color;
  }
}
