.subscription {
  position: relative;
  background: $subscription-color;
  padding: 64px 15px;
  padding-bottom: 29px;

  @media (min-width: $vp-768) {
    padding: 100px 45px;
    padding-bottom: 49px;
  }

  @media (min-width: $vp-1200) {
    padding: 123px 15px;
    padding-bottom: 80px;
  }

  @media (min-width: $vp-1366) {
    padding: 123px 103px;
    padding-bottom: 80px;
  }

  h2 {
    padding: 0;
    margin: 0;
    text-align: center;
    font-family: "Alumni Sans", "Arial", sans-serif;
    font-weight: 600;
    line-height: normal;
    color: $gym-color;
    font-size: 32px;
    letter-spacing: 0.96px;

    @media (min-width: $vp-768) {
      font-size: 42px;
      letter-spacing: 1.26px;
    }

    @media (min-width: $vp-1200) {
      font-size: 54px;
      letter-spacing: 1.62px;
    }
  }
}

.subscription__image {
  display: none;

  @media (min-width: $vp-768) {
    display: block;
    position: absolute;
    z-index: 1;
    right: 0;
    bottom: -131px;

    img {
      width: 100%;
    }
  }

  @media (min-width: $vp-1200) {
    bottom: -151px;
  }
}

.subscription__list {
  position: relative;
  left: 50%;
  transform: translate(-50%, 0);
  padding: 0;
  margin: 0;
  display: inline-flex;
  margin-left: auto;
  margin-right: auto;
  flex-wrap: wrap;
  gap: 45px;
  list-style-type: none;
  margin-top: 19px;
  padding-bottom: 9px;
  transition: 1s ease-in-out;

  @media (min-width: $vp-768) {
    gap: 53px;
  }
}

.subscription__tab {
  display: block;
  background: none;
  position: relative;
  border: none;
  padding: 0;
  margin: 0;
  color: rgba(16, 38, 84, 0.5);
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
  cursor: pointer;
  transition: $trans-default;

  &::before {
    content: "";
    position: absolute;
    bottom: -15px;
    left: 0;
    width: 190%;
    height: 2px;
    background-color: #dedfe1;
  }

  &:last-of-type::before {
    width: 100%;
  }

  &:focus-visible,
  &:hover {
    outline: none;
    color: $gym-color;

    &::after {
      content: "";
      position: absolute;
      bottom: -15px;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: $rose-color;
    }
  }

  &.is-active {
    color: $gym-color;

    &::after {
      content: "";
      position: absolute;
      bottom: -15px;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: $rose-color;
    }
  }

  @media (min-width: $vp-768) {
    font-size: 18px;

    &::before {
      bottom: -14px;
    }

    &:last-of-type {
      padding-right: 0;
    }

    &:focus-visible,
    &:hover {
      color: $gym-color;

      &::after,
      &::before {
        bottom: -14px;
      }
    }

    &.is-active {
      color: $gym-color;

      &::after {
        bottom: -14px;
      }
    }
  }

  @media (min-width: $vp-1200) {
    &::before {
      bottom: -11px;
    }

    &:focus-visible,
    &:hover {
      color: $gym-color;

      &::after,
      &::before {
        bottom: -11px;
      }
    }

    &.is-active {
      color: $gym-color;

      &::after {
        bottom: -11px;
      }
    }
  }
}

.subscription__sub-list {
  margin: 0 auto;
  padding: 40px 0;
  display: flex;
  flex-direction: column;
  list-style-type: none;
  align-items: center;
  gap: 33px;

  @media (min-width: $vp-768) {
    padding: 47px 0;
    gap: 40px;
  }

  @media (min-width: $vp-1200) {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-content: center;
    gap: 40px;
    padding-top: 34px;
  }
}

.subscription__sub-item {
  margin: 0;
  padding: 36px 32px;
  display: block;
  padding-bottom: 47px;
  background-color: $color-default-white;
  border: 7px solid transparent;

  &:hover,
  &:focus-visible,
  &:focus,
  &:active {
    outline: none;
    border: 7px solid;
    border-image: linear-gradient(100deg, #ed0233 15%, #003eb7 80%);
    border-image-slice: 1;

    & .subscription__sub-button {
      border: none;
      padding: 26px 45px;
      color: $color-default-white;
      background: linear-gradient(100deg, #ed0233 15%, #003eb7 80%);
    }
  }

  @media (min-width: $vp-768) {
    width: 442px;
    padding: 46px 68px 58px;
  }

  @media (min-width: $vp-1200) {
    max-width: 360px;
  }

  h3 {
    margin: 0;
    padding: 0;
    color: $gym-color;
    text-align: center;
    padding-top: 7px;
    font-style: normal;
    font-size: 22px;
    font-weight: 700;
    line-height: 110.16%;

    @media (min-width: $vp-768) {
      font-size: 26px;
      padding-top: 11px;
    }
  }

  p {
    margin: 0;
    padding: 0;
    padding-top: 10px;
    color: $sub-text;
    text-align: center;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
  }
}

.subscription__sub-wrapper {
  position: relative;

  p {
    margin: 0;
    padding: 0;
    position: relative;
    text-align: center;
    font-size: 48px;
    font-weight: 700;
    line-height: 150%;
    color: $price;
    padding-top: 56px;
    display: flex;
    align-items: baseline;
    justify-content: center;
    gap: 14px;
    z-index: 2;
    padding-left: 11px;

    & span {
      color: $gym-color;
    }

    @media (min-width: $vp-768) {
      padding-top: 66px;
      font-size: 60px;
    }
  }

  p:last-of-type {
    position: absolute;
    font-size: 80px;
    top: -50px;
    left: 50%;
    transform: translate(-50%, 0);
    color: $whisper;
    font-style: normal;
    font-weight: 700;
    line-height: 150%;
    z-index: 0;

    @media (min-width: $vp-768) {
      font-size: 100px;
      padding-left: 0;
    }
  }
}

.subscription__sub-button {
  display: block;
  padding: 24px 45px;
  margin-top: 29px;
  white-space: nowrap;
  font-size: 12px;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.6px;
  text-transform: uppercase;
  color: $gym-color;
  border: 2px solid rgba(16, 38, 84, 0.2);
  user-select: none;

  &:hover,
  &:focus-visible {
    border-color: $gym-color;
    outline: none;
  }

  &:active {
    border: none;
    padding: 26px 45px;
    color: $color-default-white;
    background: linear-gradient(100deg, #ed0233 15%, #003eb7 80%) t;
  }

  &:disabled {
    opacity: 0.3;
  }

  @media (min-width: $vp-768) {
    margin-top: 34px;
    width: 206px;
    padding: 24px 42px;
    margin-left: auto;
    margin-right: auto;
  }
}
